<template>
  <div>
    <ul class="list">
      <li>
        <em>油品:</em>
        <el-select v-model="query.productCategoryCode"
                   clearable
                   placeholder="请选择">
          <el-option v-for="item in options.category"
                     :key="item.code"
                     :label="item.zhName"
                     :value="item.code">
          </el-option>
        </el-select>
      </li>
      <li>
        <em>订单状态:</em>
        <el-select v-model="query.contractSheetStatusCode"
                   clearable
                   placeholder="请选择">
          <el-option v-for="item in options.status"
                     :key="item.code"
                     :label="item.zhName"
                     :value="item.code">
          </el-option>
        </el-select>
      </li>
      <li>
        <em>贸易条款:</em>
        <el-select v-model="query.tradeTermCode"
                   clearable
                   placeholder="请选择">
          <el-option v-for="item in options.tradeTerms"
                     :key="item.code"
                     :label="item.zhName"
                     :value="item.code">
          </el-option>
        </el-select>
      </li>
      <li>
        <em>卸港港口:</em>
        <el-select v-model="query.dischargePort"
                   clearable
                   placeholder="请选择">
          <el-option v-for="item in options.dischargePort"
                     :key="item.code"
                     :label="item.zhName"
                     :value="item.enName">
          </el-option>
        </el-select>
      </li>
      <li>
        <em> 订单编号:</em>
        <el-input placeholder="请输入单据编号"
                  v-model="query.serialNumber"
                  clearable>
        </el-input>
      </li>
      <li>
        <em>合同编号:</em>
        <el-input placeholder="请输入合同编号"
                  v-model="query.contractNO"
                  clearable>
        </el-input>
      </li>
      <li>
        <div class="block">
          <span class="demonstration">时间范围</span>
          <el-date-picker v-model="time"
                          type="daterange"
                          range-separator="至"
                          start-placeholder="开始日期"
                          end-placeholder="结束日期">
          </el-date-picker>
        </div>
      </li>
      <li>
        <el-button type="primary"
                   @click="tableList">查询</el-button>
        <el-button type="success"
                   @click="reset">重置</el-button>
        <el-button type="info"
                   @click="download">下载</el-button>
      </li>
    </ul>
    <el-table :data="tableData"
              border
              style="width: 100%">
      <!-- <el-table-column fixed
                       prop="number"
                       label="编号"
                       width="150">
      </el-table-column> -->
      <el-table-column prop="serialNumber"
                       label="订单编号"
                       width="120">
      </el-table-column>
      <el-table-column prop="contractNO"
                       label="合同编号"
                       width="120">
      </el-table-column>
      <el-table-column prop="contractSheetStatus"
                       label="订单状态"
                       width="120">
      </el-table-column>
      <el-table-column prop="createuser"
                       label="创建人"
                       width="300">
      </el-table-column>
      <el-table-column prop="createtime"
                       label="创建时间"
                       width="240">
      </el-table-column>
      <el-table-column prop="productCategory"
                       label="油品"
                       width="120">
      </el-table-column>
      <el-table-column prop="productSpecification"
                       label="规格"
                       width="120">
      </el-table-column>
      <el-table-column prop="premiumsAndDiscounts"
                       label="升贴水"
                       width="120">
      </el-table-column>
      <el-table-column prop="quantity"
                       label="数量"
                       width="120">
      </el-table-column>

      <el-table-column prop="tradeTerm"
                       label="贸易条款"
                       width="120">
      </el-table-column>
      <el-table-column prop="tolerance"
                       label="溢短装"
                       width="120">
      </el-table-column>
      <el-table-column prop="pricingBenchmark"
                       label="基准"
                       width="120">
      </el-table-column>
      <el-table-column prop="pricingPeriod"
                       label="计价期"
                       width="120">
      </el-table-column>
      <el-table-column prop="laycanDateAsString"
                       label="装期"
                       width="240">
      </el-table-column>
      <el-table-column prop="dischargePort"
                       label="卸港"
                       width="120">
      </el-table-column>
      <el-table-column prop="settleNum"
                       label="结算量"
                       width="120">
      </el-table-column>
      <el-table-column prop="settleAmt"
                       label="结算金额"
                       width="120">
      </el-table-column>
      <el-table-column prop="otherTerm"
                       label="其他"
                       width="120">
      </el-table-column>

      <el-table-column fixed="right"
                       label="操作"
                       width="100">
        <template slot-scope="scope">
          <el-button @click="preview(scope.row)"
                     type="text"
                     size="small">预览</el-button>

          <el-button v-if="scope.row.upInvoiceStatus==0"
                     @click="upload(scope.row)"
                     type="text"
                     size="small">上传</el-button>
          <el-button v-if="scope.row.upInvoiceStatus==1"
                     @click="edit(scope.row)"
                     type="text"
                     size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="block footer">
      <el-pagination background
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :page-sizes="[10]"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total='totalNum-0'>
      </el-pagination>
    </div>
  </div>
</template>
<script>
import url from '@/api/api'
import { formateDate } from '@/filters/index'
export default {
  data () {
    return {
      totalNum: '',
      options: {},
      tableData: [],
      time: '',
      query: {
        serialNumber: '',
        pageNum: 1,
        pageSize: 10,
        contractNO: '',
        gmtCreatedStartAsString: '',
        gmtCreatedEndAsString: '',
        productCategoryCode: '',
        tradeTermCode: '',
        contractSheetStatusCode: '',
        dischargePort: ''
      }
    }
  },
  watch: {
    value: function (val) { }
  },
  created () {
    console.log(this)
    this.screen()
    this.tableList()
  },
  methods: {
    // 下载
    download () {
      this.ajax(
        url.download,
        this.query,
        'post'
      ).then(res => {
        window.open(res.data.datas)
      })
    },
    // 上传
    upload (data) {
      this.$router.push({ name: 'show', query: { uuid: data.uuid, url: data.endpoint, state: data.upInvoiceStatus } })
    },
    // 修改
    edit (data) {
      this.$router.push({ name: 'show', query: { uuid: data.uuid, url: data.endpoint, state: data.upInvoiceStatus } })
    },
    // 预览
    preview (data) {
      this.ajax(
        url.show,
        {
          uuid: data.uuid
        },
        'get'
      ).then(res => {
        let data = res.data.datas
        this.$alert('<div class="pop"><ul>' +
          '<li>基本信息 </li>' +
          '<li>油品：<span>' + data.productCategory + '</span> </li>' +
          '<li>规格：<span>' + data.productSpecification + '</span> </li>' +
          '<li>数量：<span>' + data.quantity + '</span> </li>' +
          '<li>溢短装：<span>' + data.tolerance + '</span> </li>' +
          '<li>国际贸易条款：<span>' + data.tradeTerm + '</span> </li>' +
          '</ul>' +
          '<ul>' +
          '<li>价格 </li>' +
          '<li>计价基准：<span>' + data.pricingBenchmark + '</span> </li>' +
          '<li>升贴水：<span>' + data.premiumsAndDiscounts + '</span> </li>' +
          '<li>计价期：<span>' + data.pricingPeriod + '</span> </li>' +
          '<li>付款条件：<span>' + data.paymentTerm + '</span> </li>' +
          '<li>商检费分摊：<span>' + data.inspection + '</span> </li>' +
          '<li>Law：<span>' + data.law + '</span> </li>' +
          '<li>计价公式：<span>' + data.pricingFormula + '</span> </li>' +
          '</ul>' +
          '<ul>' +
          '<li> 船运</li>' +
          '<li> 运输方式：<span>' + data.transportMode + '</span></li>' +
          '<li> 装货港：<span>' + data.dischargePort + '</span></li>' +
          '<li> 装期:<span>' + data.laycanDateAsString + '</span></li>' +
          '<li> 滞期费率:<span>' + data.demurrageRateNum + '</span></li>' +
          '</ul>' +
          '<ul>' +
          '<li> 买家</li>' +
          '<li> 公司名称：<span>' + data.buyerName + '</span></li>' +
          '<li> 公司地址：<span>' + data.buyerAddress + '</span></li>' +
          '<li> 联系人:<span>' + data.buyerTraderName + '</span></li>' +
          '<li> 电话:<span>' + data.buyerTelephone + '</span></li>' +
          '<li> 邮箱:<span>' + data.buyerEmail + '</span></li>' +
          '<li> 传真:<span>' + data.buyerFax + '</span></li>' +
          '</ul>' +
          '<ul>' +
          '<li> 卖家</li>' +
          '<li> 公司名称：<span>' + data.salerName + '</span></li>' +
          '<li> 公司地址：<span>' + data.salerAddress + '</span></li>' +
          '<li> 联系人:<span>' + data.salerTraderName + '</span></li>' +
          '<li> 电话:<span>' + data.salerTelephone + '</span></li>' +
          '<li> 邮箱:<span>' + data.salerEmail + '</span></li>' +
          '<li> 传真:<span>' + data.salerFax + '</span></li>' +
          '</ul>' +
          '<ul>' +
          '<li> 其他信息</li>' +
          '<li> 其他条款<span>' + data.otherTerm + '</span></li>' +
          '</ul></div>', '订单详情', { dangerouslyUseHTMLString: true })
      })
    },
    // 每页条数
    handleSizeChange (val) {
      this.query.pageSize = val
    },
    // 当前第几页
    handleCurrentChange (val) {
      this.query.pageNum = val
      this.tableList()
    },
    // 查询条件
    screen () {
      this.ajax(
        url.select,
        {},
        'get'
      ).then(res => {
        this.options = res.data.datas
      })
    },
    // 渲染列表
    tableList () {
      this.query.gmtCreatedStartAsString = formateDate(this.time[0])
      this.query.gmtCreatedEndAsString = formateDate(this.time[1])
      this.ajax(
        url.tableList,
        this.query,
        'post'
      ).then(res => {
        this.tableData = res.data.datas
        this.totalNum = this.tableData.length
      })
    },
    // 清空筛选条件
    reset () {
      this.time = ''
      this.query = {
        serialNumber: '',
        pageNum: 1,
        pageSize: 10,
        contractNO: '',
        gmtCreatedStartAsString: '',
        gmtCreatedEndAsString: '',
        productCategoryCode: '',
        tradeTermCode: '',
        contractSheetStatusCode: '',
        dischargePort: ''
      }
      this.tableList()
    }
  }
}
</script>
<style lang="scss">
em {
  width: 60px;
  display: inline-block;
}
.el-input {
  width: 150px;
}

.el-input--suffix {
  width: 150px;
}
.list {
  overflow: hidden;
  padding: 15px;
  background: #ccc;
  li {
    float: left;
    padding: 15px;
  }
}
.footer {
  width: 100%;
  text-align: center;
}
.pop {
  height: 400px;
  overflow: scroll;
}
.el-popover {
  text-align: left;
}
.wd {
  width: 100px;
}
.el-table__row{
  text-align: center
}
.pop ul{
  padding: 15px 0
}
</style>



