<template>
  <div class="show">
    <h3>上传合同</h3>
    <div>
      <em>结算量:</em>
      <el-input v-model="settleNum"
                placeholder="请输入内容"
                prop="name"></el-input>
      <el-select v-model="settleNumUnit"
                 placeholder="请选择">
        <el-option v-for="item in unit1"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div>
      <em>结算金额：</em>
      <el-input v-model="settleAmt"
                placeholder="请输入内容"></el-input>
      <el-select v-model="settleAmtUnit"
                 placeholder="请选择">
        <el-option v-for="item in unit2"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="upload">
      <em>临时发票:</em>
      <el-upload :action="actionUrl"
                 list-type="picture-card"
                 :on-remove="handleRemove1"
                 :on-success='success1'
                 :file-list="proInvoiceList">
        <i class="el-icon-plus"></i>
      </el-upload>
    </div>
    <div class="upload">
      <em>结算发票:</em>
      <el-upload :action="actionUrl"
                 :limit='2'
                 list-type="picture-card"
                 :on-remove="handleRemove2"
                 :on-success='success2'
                 :file-list="settleInvoiceList">
        <i class="el-icon-plus"></i>
      </el-upload>
    </div>
    <div>
      <el-button type="primary"
                 @click="confirm">确认</el-button>
      <el-button type="info"
                 @click="cancel">取消</el-button>
    </div>
  </div>
</template>
<script>
import url from '@/api/api'
export default {
  data () {
    return {
      state: this.$route.query.state,
      url: this.$route.query.url,
      contractUUid: this.$route.query.uuid || '',
      proInvoiceList: [],
      settleInvoiceList: [],
      actionUrl: url.action,
      settleNum: '',
      settleNumUnit: '万吨',
      settleAmt: '',
      settleAmtUnit: '亿元',
      unit1: [{
        value: '万吨',
        label: '万吨'
      },
      {
        value: '万桶',
        label: '万桶'
      }],
      unit2: [{
        value: '亿元',
        label: '亿元'
      },
      {
        value: '亿美元',
        label: '亿美元'
      }]
    }
  },
  created () {
    if (this.state === '1') {
      this.ajax(
        url.edit,
        {
          contractUUid: this.contractUUid
        },
        'get'
      ).then(res => {
        this.settleNum = res.data.datas.settleNum
        this.settleNumUnit = res.data.datas.settleNumUnit
        this.settleAmt = res.data.datas.settleAmt
        this.settleAmtUnit = res.data.datas.settleAmtUnit
        res.data.datas.proInvoiceList.forEach(e => {
          e.url = e.endpoint + '/' + e.filePath
        })
        res.data.datas.settleInvoiceList.forEach(e => {
          e.url = e.endpoint + '/' + e.filePath
        })
        this.settleInvoiceList = res.data.datas.settleInvoiceList
        this.proInvoiceList = res.data.datas.proInvoiceList
      })
    }
  },
  methods: {
    format (data) {
      let arr = []
      data.forEach(e => {
        let obj = {}
        obj.fileName = e.fileName
        obj.filePath = e.filePath
        arr.push(obj)
      })
      return arr
    },
    handleRemove1 (file, fileList) {
      for (var i = 0; i < this.proInvoiceList.length; i++) {
        if (this.proInvoiceList[i].filePath === file.filePath) {
          this.proInvoiceList.splice(i, 1)
        }
      }
    },
    handleRemove2 (file, fileList) {
      for (var i = 0; i < this.settleInvoiceList.length; i++) {
        if (this.settleInvoiceList[i].filePath === file.response.datas.path) {
          this.settleInvoiceList.splice(i, 1)
        }
      }
    },
    success1 (res) {
      let obj = {}
      obj.fileName = res.datas.originalName
      obj.filePath = res.datas.path
      obj.url = this.url + '/' + res.datas.path
      this.proInvoiceList.push(obj)
    },
    success2 (res) {
      let obj = {}
      obj.fileName = res.datas.originalName
      obj.filePath = res.datas.path
      obj.url = this.url + '/' + res.datas.path
      this.settleInvoiceList.push(obj)
    },
    confirm () {
      let obj = {
        contractUUid: this.contractUUid,
        proInvoiceList: this.format(this.proInvoiceList),
        settleInvoiceList: this.format(this.settleInvoiceList),
        settleNum: this.settleNum,
        settleNumUnit: this.settleNumUnit,
        settleAmt: this.settleAmt,
        settleAmtUnit: this.settleAmtUnit
      }
      this.ajax(
        url.upload,
        obj,
        'post'
      ).then(res => {
        this.$message({
          message: '上传成功',
          type: 'success'
        })
        this.$router.go(-1)
      })
    },
    cancel () {
      this.$router.go(-1)
    }

  }
}
</script>
<style lang="scss">
.el-input {
  width: 200px;
}
.el-input--suffix {
  width: 200px;
}
.show {
  width: 600px;
  margin: 0 auto;
}
em {
  display: inline-block;
  width: 80px;
  padding: 10px 0;
}
h3 {
  text-align: center;
  padding: 15px;
}
</style>



