export function zeroizeOfTen (n) {
  return n < 10 ? ('0' + n) : n
}
export function formateDate (time) {
  let result = ''
  if (time) {
    let date = new Date(time)

    let y = date.getFullYear()
    let m = date.getMonth() + 1
    let d = date.getDate()

    m = zeroizeOfTen(m)
    d = zeroizeOfTen(d)

    result = y + '-' + m + '-' + d
  }
  return result
}



