import Vue from 'vue'
import Router from 'vue-router'
import screen from '../components/screen.vue'
import show from '../components/show.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: screen
    },
    {
      path: '/screen',
      name: 'screen',
      component: screen
    },
    {
      path: '/show',
      name: 'show',
      component: show
    }
  ]
})



